import { useCallback } from "react"
import { useQueryClient } from "@tanstack/react-query"
import { filesQueryKeys } from "../utils"

type UseInvalidateSharedFiles = () => void

const useInvalidateSharedFiles = (): UseInvalidateSharedFiles => {
  const queryClient = useQueryClient()
  const invalidate = useCallback(() => {
    queryClient.invalidateQueries({
      queryKey: filesQueryKeys.paginated({ shared: true }).slice(0, 2),
      exact: false,
    })
  }, [queryClient])

  return invalidate
}

export { useInvalidateSharedFiles }
export type { UseInvalidateSharedFiles }
